body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: "Times New Roman", Times, serif;
}

.App {
  background-color: rgb(250, 236, 236);
}

.bookSearch {
  color: #003399;
  text-align: center;
  background-color: #ccffcc;
  /* padding-bottom: 15px; */
  padding-top: 15px;
  height: 28vh;
}

.bookDisplay {
  text-align: center;
  margin-top: 20px;
}

.card-header {
  background-color: #ccffcc;
}

.thumbnail {
  size: 20px;
}
.card {
  margin-bottom: 15px;
}

.bookTitle {
  color: #003399;
  font-size: 30px;
}

.bookCards {
  border-width: thick;
  border-style: solid;
  border-color: #0099cc;
}

.authors {
  color: #0099cc;
  font-size: 20px;
  font-weight: 700;
}

.description {
  padding: 30px;
  background-color: #0099cc;
  font-size: 20px;
}

.savedComponent {
  color: #003399;
  text-align: center;
  background-color: #ccffcc;
  /* padding-bottom: 15px; */
  padding-top: 15px;
  height: 10vh;
}

.bookDisplay {
  text-align: center;
  margin-top: 20px;
}

.card-header {
  background-color: #ccffcc;
}

.navigation {
  display: flex;
  justify-content: space-around;
  height: 10vh;
  background-color: #0099cc;
}

.logo {
  display: flex;
  align-items: center;
  color: #003399;
  font-size: 45px;
  font-weight: 700;
}

.nav_links {
  display: flex;
  align-items: center;
}

.link {
  font-size: 25px;
  color: #ccffcc;
}

.link:not(:last-child) {
  margin-right: 30px;
}

.link:hover {
  cursor: pointer;
}

